.wrapper {
  margin: 0 auto;
  max-width: 320px;
  padding: 0 20px;

  @include onTablet {
    max-width: 768px;
    padding: 0 39px;
  }

  @include onDesktop {
    max-width: 1024px;
    padding: 0 39px;
  }

  @include onLarge {
    max-width: 1280px;
    padding: 0 55px;
  }
}
